<template>
    <div class="card">
        <div class="card-header rounded-bottom rounded-4 d-flex justify-content-between">
            <h3 class="py-2">Mots cls actuelle</h3>
            <div class="a">
                <!-- Bouton de mise  jour qui dclenche le modal -->
                <button class="btn m-2" @click="showUpdateModal"><i class="bi bi-pen-fill"></i></button>
                <!-- Bouton de suppression -->
                <button class="btn" @click="deleteMetaDescription"><i class="bi bi-file-earmark-x-fill"></i></button>
            </div>
        </div>
        <div class="card-body">
            <blockquote class="blockquote mb-0">
                <p v-if="metaData">{{ metaData.description }}</p>
                <p v-else>Aucune description disponible pour ce type.</p>
            </blockquote>
        </div>

        <!-- Modal de mise  jour -->
        <div class="modal fade" id="updateModal" tabindex="-1" aria-labelledby="updateModalLabel" aria-hidden="true">
            <div class="modal-dialog">
                <div class="modal-content">
                    <div class="modal-header">
                        <h5 class="modal-title" id="updateModalLabel">Modifier la mta description</h5>
                        <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
                    </div>
                    <div class="modal-body">
                        <form @submit.prevent="updateMetaDescription">
                            <div class="mb-3">
                                <label for="metaDescription" class="form-label">Mta description</label>
                                <textarea v-model="updatedDescription" class="form-control" id="metaDescription" rows="3"></textarea>
                            </div>
                            <button type="submit" class="btn btn-primary">Enregistrer</button>
                        </form>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import { fetchData, putData, deleteData } from '@/service/apiService'; // Import des fonctions PUT et DELETE
import * as bootstrap from 'bootstrap';

export default {
    name: 'Mots_cles',
    props: {
        types: {
            type: String,
            required: true
        }
    },
    data() {
        return {
            metaData: null,
            updatedDescription: '',
            type: 'mots cles',
        };
    },
    mounted() {
        this.fetchMetaDescription();
    },
    methods: {
        async fetchMetaDescription() {
            try {
                const response = await fetchData(`/configuration/search?name=${encodeURIComponent(this.types)}`);
                if (response) {
                    this.metaData = response;
                    this.updatedDescription = this.metaData.description; // Prremplir le modal avec la mta description actuelle
                }
            } catch (error) {
                console.error('Erreur lors de la rcupration des donnes:', error);
            }
        },
        showUpdateModal() {
            const updateModal = new bootstrap.Modal(document.getElementById('updateModal')); // Initialiser le modal
            updateModal.show();
        },
        async updateMetaDescription() {
            try {
                await putData(`/configuration/update_configuration/${encodeURIComponent(this.types)}`, {
                    types: this.types,
                    description: this.updatedDescription
                });
                window.alert('Mise  jour russie');
                this.fetchMetaDescription(); // Rafrachir les donnes aprs la mise  jour
                const updateModal = bootstrap.Modal.getInstance(document.getElementById('updateModal'));
                updateModal.hide(); // Fermer le modal aprs la mise  jour
            } catch (error) {
                console.error('Erreur lors de la mise  jour:', error);
                window.alert('chec de la mise  jour');
            }
        },
        async deleteMetaDescription() {
            try {
                await deleteData(`/configuration/delete_configuration/${encodeURIComponent(this.types)}`);
                window.alert('Suppression russie');
                this.metaData = null; // Rinitialiser les donnes aprs la suppression
            } catch (error) {
                console.error('Erreur lors de la suppression:', error);
                window.alert('chec de la suppression');
            }
        }
    }
};
</script>

<style scoped>
.card {
    border-radius: 15px;
}
.card-header {
    background-color: #022D7E;
    color: white;
}
button {
    background-color: #28aabb;
    color: white;
}
</style>
