import axios from 'axios';

const api_url = axios.create({
    baseURL: 'https://mobilosoft.up.railway.app/api',
    headers: {
        'Content-Type': 'application/json',
    },
    withCredentials: true
});

export const fetchData = async (endpoints) => {
    try {
        const response = await api_url.get(endpoints);
        return response.data;
    } catch (error) {
        console.error('Erreur lors de la rcupration des donnes:', error);
        throw error;
    }
};

export const postData = async (endpoint, data) => {
    try {
        const response = await api_url.post(endpoint, data);
        return response.data;
    } catch (error) {
        console.error('Erreur lors de l\'envoi des donnes:', error);
        throw error;
    }
};

// Ajout de la fonction PUT
export const putData = async (endpoint, data) => {
    try {
        const response = await api_url.put(endpoint, data);
        return response.data;
    } catch (error) {
        console.error('Erreur lors de la mise  jour des donnes:', error);
        throw error;
    }
};

// Ajout de la fonction DELETE
export const deleteData = async (endpoint) => {
    try {
        const response = await api_url.delete(endpoint);
        return response.data;
    } catch (error) {
        console.error('Erreur lors de la suppression des donnes:', error);
        throw error;
    }
};
